/**************** PAGE SETUP ****************/

body, html {
    font-family: 'Oranienbaum', serif;
    font-size: 100px;
    scroll-behavior: smooth;
    scroll-snap-type: y mandatory;
    overflow: auto;
    cursor: default;
}

.background {
    background-color: white;
    width: 100%;
    height: 100%;
    text-align: left;
}

.text {
    font-family: 'Cutive Mono', monospace;
    font-size: 20px;
    margin-top: 0;
}

.home-button {
    float: left;
    font-size: 30px;
    text-decoration: none;
    color: black;
    transition: .5s ease;
}

.home-button:hover {
    color: #84A8CC;
    transition: .5s ease;
    cursor: pointer;
}

.next-button {
    float: right;
    text-align: right;
}

.button-box {
    position: fixed;
    bottom: 0;
    background-color: white;
    text-align: left;
    width: 45%;
    height: 40px;
    padding-left: 2.5%;
    padding-right: 2.5%;
}

.left {
    position: fixed;
    width: 46.5%;
    height: 98%;
    margin: 1%;
    margin-left: 2.5%;
    overflow-y: scroll;
    overflow-x: scroll;
}

/**************** HOME ****************/

.home-screen {
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}

.home-picture {
    opacity: 0.9;
}

.background-text {
    color: white;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    text-align: center;
    transform: translate(-50%, -50%);
}

.home-title {
    margin: auto;
}

.home-route-button {
    color: white;
    text-decoration: none;
    font-size: 25px;
    transition: .5s ease;
}

.home-route-button:hover {
    color: #84A8CC;
    transition: .5s ease;
    cursor: pointer;
}

/**************** ABOUT ME ****************/

.about-left {
    margin-top: 5%;
}

.about-title {
    display: inline-block;
    position: relative;
    z-index: 1;
    margin-left: 50%;
    margin-bottom: 2.5vh;
}

.about-vertical {
    margin-top: 5vh;
    position: absolute;
    z-index: 0;
}

.about-horizontal {
    z-index: -1;
    margin-left: 10vw;
}

.about-right {
    position: absolute;
    width: 50%;
    height: 100%;
    left: 50%;
    background-color: #ACC8E5;
    overflow-y: scroll;
}

.about-blurb {
    margin: 5%;
}

.contact-button-box {
    display: block;
    text-align: center;
}

.contact-buttons {
    display: inline-block;
    margin: 5%;
}

.contact-links {
    position: relative;
}

.contact-links::after {
    content: "";
    position: absolute;
    bottom: 50px;
    left: 25px;
    width: 0px;
    border-bottom: 2.5px solid black;
    transition: .5s;
}

.contact-links:hover::after {
    width: 50px;
}

/**************** PROJECTS ****************/

.project-title {
    text-align: center;
    margin: auto;
}

.project-right {
    position: absolute;
    width: 50%;
    height: 100%;
    left: 50%;
    background-color: #ACC8E5;
    overflow-y: scroll;
}

.project-scroll {
    margin: 5%;
}

/**************** INDIVIDUAL PROJECTS ****************/

.iproject-button {
    display: inline-block;
    width: 250px;
    height: 250px;
    background-color: #ACC8E5;
    border: none;
    margin-right: 25px;
    margin-bottom: 25px;
    text-align: center;
    color: black;
    transition: 1s ease;
    text-decoration: none;
}

.iproject-container {
    position: relative;
    width: 100%;
    height: 100%;
}

.iproject-overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: .5s ease;
    opacity: 0;
}

.iproject-overlay:hover {
    background-color: white;
    transition: .5s ease;
    opacity: 1;
}

.iproject-title-text {
    font-family: 'Oranienbaum', serif;
    font-size: 25px;
    margin-top: 30%;
}

/**************** COFFEE ****************/

.coffee-gif {
    display: block;
}

.coffee-right {
    position: absolute;
    background-color: #ACC8E5;
    height: 100%;
    left: 50%;
    overflow-y: scroll;
}

.coffee-blurb {
    background-color: white;
    margin: 10px;
    padding: 2.5%;
    height: 90%;
}

.coffee-link {
    color: #ACC8E5;
    text-decoration: none;
    cursor: pointer;
    transition: .5s ease;
}

.coffee-link:hover{
    color: black;
    transition: .5s ease;
}

/**************** PHOTOGRAPHY ****************/

.photo-left {
    background-color: #ACC8E5;
    text-align: center;
    width: 50%;
    margin: 0;
}

.photo-button {
    display: block;
    margin: auto;
    background-color: #ACC8E5;
    border: none;
    color: black;
    transition: .5s ease;
    text-decoration: none;
    font-size: 25px;
}

.photo-button:hover {
    color: white;
    transition: .5s ease;
    cursor: pointer;
}

.photo-button-on {
    color: white;
}

.photo-button-on:hover {
    cursor: default;
}

.photo-right {
    position: absolute;
    left: 50%;
    width: 50%;
    height: 100%;
    overflow-y: scroll;
}

/**************** INDIVIDUAL PHOTOS ****************/

.iphoto {
    display: inline-block;
    margin-top: 2.5%;
    margin-left: 2.5%;
}
